import EmotionTheme from './emotion/EmotionTheme';
import GlobalStyles from './emotion/GlobalStyles';
import styled from '@emotion/styled';
import { useRef, useState } from 'react';

function App() {
  const [inputVal, setInputVal] = useState('https://www.freecodecamp.org');
  const formRef = useRef(null);

  const onSubmit = (e) => {
    e.preventDefault();

    fetch('https://url-shortener-microservice-gm/api/shorturl/new', {
      method: 'POST',
      body: `url=${inputVal}`,
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
      },
    });
  };

  return (
    <EmotionTheme>
      <GlobalStyles />
      <AppStyled>
        <h1>API Project: URL Shortener Microservice</h1>
        <h2>Short URL Creation </h2>
        <p>
          <code>example: POST https://url-shortener-microservice-gm/api/shorturl/new - https://www.google.com</code>
        </p>

        <form className="form" onSubmit={onSubmit} ref={formRef}>
          <span>URL to be shortened</span>
          <input type="text" onChange={(e) => setInputVal(e.target.value)} value={inputVal} />
          <button type="submit">post url</button>
        </form>

        <h2>Example Usage:</h2>
        <a href="[this_project_url]/api/shorturl/3" target="_blank" rel="noreferrer">
        https://url-shortener-microservice-gm/api/shorturl/3
        </a>
        <h2>Will Redirect to:</h2>
        <a href="https://forum.freecodecamp.org/">https://forum.freecodecamp.org/</a>

        <footer>
          <strong>
            by{' '}
            <a href="https://www.linkedin.com/in/galchenko-max/" target="_blank" rel="noreferrer">
              Maksym Galchenko
            </a>
          </strong>
        </footer>
      </AppStyled>
    </EmotionTheme>
  );
}

const AppStyled = styled.div`
  text-align: center;
  font-size: 16px;
  line-height: 1.4;
  padding: 20px;
  font-family: Roboto;
  color: #303a52;
  background-color: #f0f0f0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  box-sizing: border-box;

  hr {
    color: #303a52;
  }

  h1,
  h2 {
    margin: 0.5em 0;
  }

  code {
    background-color: #c5e3f6;
    border-radius: 3px;
    margin-bottom: 1em;
    display: inline-block;
  }

  footer {
    ${({ theme }) => theme.flex.center};
    padding-top: 2em;
  }

  a {
    color: #3d5af1;
    text-decoration: underline;
    margin-bottom: 0.3em;
    display: inline-block;
  }

  .form {
    ${({ theme }) => theme.flex.between};
    flex-wrap: wrap;
    border: 1px solid #303a52;
    border-radius: 10px;
    max-width: 600px;
    margin: 0 auto;
    padding: 2em;

    span {
      font-weight: 500;
    }

    button {
      text-transform: uppercase;
      padding: 0.3em;
    }

    input {
      padding: 10px 20px;
      font-size: 12px;
      flex-grow: 1;
      margin: 0 50px;
    }
  }
`;

export default App;
